.sqonBuilder {
  font-size: 12px;
  padding: 10px;
}
.sqonBuilder .actionHeaderContainer {
  display: flex;
  justify-content: space-between;
}
.sqonBuilder .actionHeaderContainer .button {
  margin-left: 5px;
}
.sqonBuilder .button {
  border-radius: 100px;
  box-shadow: none;
  border: solid 1px lightgrey;
  color: #008199;
  cursor: pointer;
}
.sqonBuilder .button:disabled {
  background: #f4f5f8;
  color: grey;
  cursor: unset;
}
.sqonBuilder .sqonListActionButton {
  color: #008199;
  border: none;
  padding: 5px;
  text-transform: uppercase;
  cursor: pointer;
}
.sqonBuilder .sqonListActionButton:disabled {
  color: lightgrey;
}

.sqonEntry {
  display: flex;
  flex-direction: row;
  position: relative;
  background: #f4f5f8;
  border: solid 1px lightgrey;
  margin-top: 4px;
  margin-bottom: 4px;
  padding-right: 100px;
  min-height: 40px;
}
.sqonEntry:hover {
  background: #e9e9f5;
}
.sqonEntry.active {
  background: #e4f1e7;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.sqonEntry .actionButtonsContainer {
  position: absolute;
  width: 100px;
  right: 0px;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  background: white;
  padding: 5px;
  border-left: solid 1px lightgrey;
  -webkit-animation: fadeIn 0.25s;
          animation: fadeIn 0.25s;
}

.sqonEntry .actionButtonsContainer.deleteConfirmation {
  width: 400px;
}
.sqonEntry .actionButtonsContainer.deleteConfirmation .button.delete {
  font-size: 12px;
  background: #008199;
  color: white;
}

.sqonEntry .actionButtonsContainer .sqonListActionButton {
  font-size: 18px;
}

.sqonEntry .activeStateIndicator {
  width: 15px;
  min-width: 15px;
  background: #a9adc0;
}
.sqonEntry.active .activeStateIndicator {
  background: #40a75a;
}

.sqonEntry .selectionContainer {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  width: 50px;
  max-width: 50px;
  min-width: 50px;
  padding: 2px;
  border-left: solid 1px lightgrey;
  border-right: solid 1px lightgrey;
  cursor: pointer;
}
.sqonEntry.active .selectionContainer {
  background: white;
}

.sqonEntry .sqonView {
  flex: 1 1;
  padding: 5px;
}

.sqonView .fieldOp {
}
.sqonView .fieldOp .fieldName {
}
.sqonView .fieldOp .opName {
}
.sqonView .fieldOp .opContainer {
  display: inline-block;
  padding: 5px;
  border-right: solid 1px lightgrey;
  white-space: nowrap;
  flex: 1 1;
}
.sqonView .fieldOp .selectionContainer {
  position: relative;
  padding: 0px;
  max-width: 150px;
  width: 150px;
  padding-right: 5px;
  display: inline-flex;
  justify-content: space-between;
}
.sqonView .fieldOp .valueDisplay {
  position: relative;
  padding: 5px;
  display: inline-block;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  flex: 1 1;
}
.sqonView .pillRemoveButton {
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
  border-left: solid 1px lightgrey;
  cursor: pointer;
}

.sqonView .pill {
  margin: 1px;
  display: inline-flex;
  border: solid 1px lightgrey;
  border-radius: 10px;
  background: white;
}
.sqonView .pill.sqonReference {
  overflow: hidden;
}
.sqonView .pill .content {
  padding: 5px;
  display: inline-flex;
  align-items: center;
}

.sqonView .logicalOpSelector {
  position: relative;
  cursor: pointer;
}
.sqonView .logicalOpSelector .content {
  text-transform: uppercase;
}
.sqonView .logicalOpSelector .content .opName {
  padding-right: 5px;
}
.sqonView .logicalOpSelector .menuContainer {
  position: absolute;
  top: 100%;
  background: white;
  border: solid 1px lightgrey;
  z-index: 1;
}
.sqonView .logicalOpSelector .menuOption {
  padding: 5px 10px;
}
.sqonView .logicalOpSelector .menuOption:hover {
  background: lightgray;
}
.sqonView .fieldFilterContainer {
  position: absolute;
  top: 100%;
  right: 0px;
  z-index: 1;
  cursor: default;
}
.sqonView .nestedOpBracket {
  font-weight: bold;
  font-size: 15px;
  color: #008199;
  padding-left: 3px;
  padding-right: 3px;
}

div {
  font-family: 'Montserrat', sans-serif;
}
.portal {
  flex-grow: 1;
}
/***********************************/
/* styling for the table component */
/***********************************/

.scroll-react-table .ReactTable {
  flex-grow: 1;
}

.ReactTable {
  font-family: 'Montserrat', sans-serif;
  font-size: 0.8rem;
  background: #fff;
}

.ReactTable .rt-tbody .rt-tr-group {
  border-bottom: none;
}

.ReactTable .rt-tbody .rt-td {
  font-family: 'Open Sans', sans-serif;
  border-right: 1px solid #cacbcf;
}

.ReactTable .rt-tbody .rt-td :first-child {
  font-family: 'Open Sans', sans-serif;
}

.ReactTable.-striped .rt-th {
  font-weight: bolder;
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #cacbcf;
  border-right: 1px solid #cacbcf;
}

.ReactTable .rt-resizable-header-content {
  color: #404c9a;
}

.ReactTable .rt-thead {
  background-color: #edeef1;
}

.ReactTable.-striped .rt-tr.-odd {
  background-color: #f4f5f8;
}
.ReactTable.-striped .rt-tr.-even {
  background-color: #fff;
}

.ReactTable .-pagination {
  height: 64px;
}
.ReactTable .-pagination .-pageJump {
  border: solid 1px #cacbcf;
  border-radius: 10px;
  height: 30px;
  display: flex;
  overflow: hidden;
}
.ReactTable .-pagination_button {
  display: flex;
  align-items: center;
  cursor: pointer;
  color: #74757d;
  border-right: solid 1px #cacbcf;
  min-width: 34px;
  padding-left: 0px;
  padding-right: 0px;
  align-items: center;
  justify-content: center;
}
.ReactTable .-pagination_button.-current {
  background: #f0f1f6;
  color: #009bb8;
}
.ReactTable .-pagination_button.-toEnd {
  border: none;
}

.ReactTable .-pagination_button.-disabled {
  cursor: default;
}
.ReactTable a {
  color: #90278e;
}
.ReactTable .pagination-bottom {
  background: white;
  height: 64px;
}

/***********************************/
/** styling for the aggregations ***/
/***********************************/

.aggregation-card {
  padding: 10px;
  margin-top: 5px;
  margin-bottom: 5px;
  background: white;
  border-style: solid;
  border-color: #d4d6dd;
  border-width: 1px 0 1px 0;
  border-left: solid 5px;
}
.aggregation-card:first-child {
  margin-top: 0;
}
.aggregation-card:nth-child(5n + 1) {
  border-left-color: #a42c90;
}
.aggregation-card:nth-child(5n + 2) {
  border-left-color: #00afed;
}
.aggregation-card:nth-child(5n + 3) {
  border-left-color: #ff9324;
}
.aggregation-card:nth-child(5n + 4) {
  border-left-color: #009bb8;
}
.aggregation-card:nth-child(5n + 5) {
  border-left-color: #d8202f;
}
.aggregation-card .header {
  margin-top: -10px;
}
.aggregation-card .title-wrapper {
  padding-top: 10px;
  padding-bottom: 7px;
  color: #404c9a;
}
.aggregation-card .title-wrapper,
.aggregation-card .filter {
  border-bottom: solid 1px #e0e1e6;
}
.aggregation-card .title-wrapper.collapsed {
  padding-bottom: 0px;
  border-bottom: none;
}
.aggregation-card .title-control {
  display: flex;
  cursor: pointer;
}
.aggregation-card .title-wrapper .title {
  color: #404c9a;
  font-weight: bolder;
  font-size: 0.9rem;
  font-family: 'Open Sans', sans-serif;
  margin-left: 5px;
}
.aggregation-card .filter {
  padding: 7px 0;
}
.aggregation-card input[type='checkbox'] {
  pointer-events: none;
  margin-right: 5px;
  flex-shrink: 0;
  vertical-align: middle;
}
.aggregation-card .filter .showMore-wrapper {
  margin-left: 5px;
  margin-top: 0px;
}
.aggregation-card .bucket {
  padding-top: 5px;
}
.aggregation-card .bucket.collapsed {
  padding-top: 0px;
}
.aggregation-card .bucket-item {
  padding-top: 5px;
  padding-bottom: 5px;
  cursor: pointer;
}
.aggregation-card .bucket-item .bucket-count {
  border-radius: 5px;
  background-color: rgba(169, 173, 192, 0.3);
  color: #343434;
  padding-left: 10px;
  padding-right: 10px;
  font-family: 'Open Sans', sans-serif;
  display: flex;
  align-items: center;
  margin-left: auto;
}
.aggregation-card .bucket .header {
  font-size: 11px;
  margin-top: 4px;
  text-transform: uppercase;
}
.showMore-wrapper {
  margin-top: 5px;
  font-size: 0.8rem;
  color: #90278e;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: flex-end;
